.Rolldice {
    display: flex;
    flex-flow: column nowrap;
    &-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: nowrap;
    }
    button {
        width: 15em;
        padding: 1.5em;
        border: 0;
        border-radius: 10px;
        color: white;
        background-color: black;
        margin-top: 3em;
        align-self: center;
        outline: none;
        cursor: pointer;
        transition: background-color .5s;
        &:hover {
            background-color: slateblue;
        }
    }
}